.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: inset 0 1px 3px -2px black;

}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: black;
    font-size: 38px;
    font-weight: 600;

}
.nav-menu {
    display: flex;
    flex-direction: row;  /* makes them side-by-side */
    gap: 50px;            /* space between items */
    list-style: none;     /* remove bullets */
    padding: 0;
    margin: 0;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}

.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    height: 3px;
    border-radius: 10px;

    width: 80%;
    background-color: red;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: grey;
    font-size: 30px;
    font-weight: 800;
    background: white;
    cursor: pointer;

}
.nav-login-cart button:active{
    background: #f3f3f3;
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}
.admin{
    display: flex;
    align-items: center;
    gap: 45px;
}
.admin button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: grey;
    font-size: 30px;
    font-weight: 800;
    background: white;
    cursor: pointer;

}
.admin button:active{
    background: #f3f3f3;
}