.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:10px;
    height:90vh;

}
.popular h1{
    margin-top: 100px;
    color: black;
    font-size: 50px;
    font-weight: 600;

}
.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;

}
.popular_item{
    margin-top: 50px;
    display: flex;
    gap: 20px;
}